.box_area {
  width: 50px;
  height: 50px;
  box-shadow: 4px 4px 8px 0px rgba(247, 243, 243, 0.2);
  box-sizing: border-box;
  background: rgb(252, 252, 176);
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
}
.game_field {
  width: 300px;
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
}
.hide_area {
  background: white;
}
.show_area img {
  opacity: 30;
}
img {
  width: 35px;
  height: 35px;
  margin: 10px 10px 10px 8px;
  opacity: 0;
} 
.notify {
  display: none;
}
.notify_show {
  display: block;
}
